@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;700&display=swap');

*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, input, textarea {
  font-family: 'Manrope', sans-serif;
}

input, button {
  background-color: transparent;
  border: none;
  transition: all 0.1s ease-in;
}

h1 {font-size: 2rem; font-weight: 400;}
h2 {font-size: 1.6rem; font-weight: 400;}
h3 {font-size: 1.2rem; font-weight: 400;}
h4 {font-size: 1.1rem; font-weight: 400;}
h5 {font-size: 1.0rem; font-weight: 400;}
h6 {font-size: 0.9rem; font-weight: 400;}
p {font-size: 0.7rem; font-weight: 400;}


@media (max-width: 1080px) {
  h1 {font-size: 1.9rem;}
  h2 {font-size: 1.5rem;}
  h3 {font-size: 1.1rem;}
  h4 {font-size: 1.0rem;}
  h5 {font-size: 0.9rem;}
  h6 {font-size: 0.8rem;}
  p {font-size: 0.65rem;}
}

@media (max-width: 880px) {
  h1 {font-size: 1.8rem;}
  h2 {font-size: 1.4rem;}
  h3 {font-size: 1rem;}
  h4 {font-size: 0.9rem;}
  h5 {font-size: 0.85rem;}
  h6 {font-size: 0.75rem;}
  p {font-size: 0.6rem;}
}

@media (max-width: 760px) {
  h1 {font-size: 1.7rem;}
  h2 {font-size: 1.3rem;}
  h3 {font-size: 0.95rem;}
  h4 {font-size: 0.85rem;}
  h5 {font-size: 0.80rem;}
  h6 {font-size: 0.70rem;}
  p {font-size: 0.55rem;}
}