.cabezera {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
}

button:hover {
    opacity: 0.75;
}

.boton {
    background: linear-gradient(to right top, #754cf1, #b555f7);
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
    transition: all 0.1s ease-in;
}

.flex-centrado {
    display: flex;
    align-items: center;
    justify-content: center;
}

.boton:hover {
    background: linear-gradient(to right top, #b555f7, #754cf1);
}

.scrollbar::-webkit-scrollbar {
    width: 8px;
    scroll-behavior: smooth;
  }

.scrollbar::-webkit-scrollbar-track {
    background: #f1f1f1;
    border: 0.5px solid #cccdce;
    border-radius: 5px;
  }

.scrollbar::-webkit-scrollbar-thumb {
    background: linear-gradient(to bottom, #754cf1, #b555f7);
    border-radius: 5px;
  }

  .scrollbar::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(to top, #815bf0, #c36ffb);
  }
  
.desconectado {
    height: 20px; 
    background-color: red;
    color: white;
}

.center-text {
    text-align: center;
}

.padding-5 {
    width: 90%;
    margin: 0 5%;
}



.flex {
    display: flex;
}

.panel-chats {
    flex-basis: 20%;
    /* flex-grow: 1; */
}

.panel-chats .cabezera-chat{
    height: 8.5vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.panel-chats .cabezera-chat svg {
    height: 25px;
    width: 25px;
    color: #a8adb3;
}

.panel-chats .cabezera-chat svg:nth-child(2) {
    color: #904ff3;
}


.panel-chats .show-chats .label-chats {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 15px 0;
}

.panel-chats .show-chats .tipo-chat {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.panel-chats .show-chats .tipo-chat p {
    text-transform: uppercase;
}

.panel-chats .show-chats .tipo-chat span {
    color: red;
}

.panel-chats .show-chats .label-chats svg {
    color: #904ff3;
    height: 1.8rem;
    width: 1.8rem;
}

.panel-chats .show-chats .buscar-chat {
    display: flex;
    box-shadow: 4px 2px 15px 0px #cccdce;
    align-items: center;
    border-radius: 10px;
    margin: 15px 0;

}
.panel-chats .show-chats .buscar-chat svg {
    height: 12.5px;
    width: 12.5px;
}

.panel-chats .show-chats .buscar-chat input {
    border: none;
    background-color: transparent;
    height: 25px;
    width: calc(100% - 12.5px);
    border-radius: 0 20px 20px 0;
}

.panel-chats .show-chats .chats {
    overflow-y: scroll;
    height: 60vh;
    width: 100%;
}

.panel-chats .show-chats .chats .chat {
    box-shadow: 0px 0px 5px 2px #cccdce;
    display: flex;
    justify-content: space-between;
    padding: 5px 1px;
    margin-bottom: 10px;
    border: 0.1px solid #cccdce;
    border-radius: 5px;
    /* gap: 10px; */
}

.panel-chats .show-chats .chats .chat .img-info {
    display: flex;
    align-items: center;
}

.panel-chats .show-chats .chats .chat .img-info .info {
    margin-left: 5px;
    text-align: left;
}

.panel-chats .show-chats .chats .chat .img-info .info p {
    opacity: 0.8;
}

.panel-chats .show-chats .chats .chat .detalle {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

.panel-chats .show-chats .chats .chat .detalle > p {
    color: #904ff3;
}

.panel-chats .show-chats .chats .chat .detalle .cuenta {
    background: linear-gradient(to right, #754cf1, #b555f7);;
    height: 20px;
    width: 20px;
    text-align: center;
    border-radius: 50%;
    color: white;
}

.mensajes {
    flex-basis: 60%;
    /* flex-grow: 12; */
    border-left: .5px solid #ececec;
    border-right: .5px solid #ececec;
}

.mensajes .cabezera-mensaje {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.mensajes .cabezera-mensaje .label-mensaje {
    height: 8.5vh;
    display: flex;
    gap: 5px;
    align-items: center;
}

.mensajes .cabezera-mensaje .iconos {
    display: flex;
    gap: 5px;
}

.mensajes .cabezera-mensaje .iconos svg {
    height: 25px;
    width: 25px;
    color: #a8adb3;
}

.mensajes .mensaje-chat {
    height: 72.5vh;
    border-top: .5px solid #cccdce;
    border-bottom: .5px solid #cccdce;
    overflow-y: scroll;
}

.mensajes .mensaje-chat .mensaje {
    background-color: #f2f6fa;
    width: 90%;
    margin: 10px 6% 10px 3%;
    padding: 5px 10px;
    border-radius: 15px 15px 15px 15px;
    box-shadow: 1px 1px 6px 0px #a8adb3;
    position: relative;
}

.mensajes .mensaje-chat .nopropio::before {
    content: '';
    height: 0px;
    width: 0px;
    border-right: 15px solid #f2f6fa;
    border-top: 7.5px solid transparent;
    border-bottom: 15px solid transparent; 
    position: absolute;
    left: -14px;
    top: 10px;
}

.mensajes .mensaje-chat .propio::before {
    content: '';
    height: 0px;
    width: 0px;
    border-left: 15px solid #938af8;
    border-top: 7.5px solid transparent;
    border-bottom: 15px solid transparent; 
    position: absolute;
    right: -14px;
    top: 10px;
}

.mensajes .mensaje-chat .propio {
    background-color: #938af8;
    margin: 10px 3% 10px 6%;
}

.left {
    text-align: left;
}

.right {
    text-align: right;
}

.morado {
    color: #754cf1;
}

.mensajes .mensaje-chat .mensaje h4 {
    color: #754cf1;
}

.mensajes .mensaje-chat .mensaje .hora {
    opacity: 0.7;
}

.mensajes .form-mensaje {
    width: 100%;
    position: relative;
}

.mensajes .form-mensaje button {
    border: none;
    border-radius: 50%;
    background: linear-gradient(to right, #754cf1, #b555f7);
    width: 35px;
    height: 35px;
    position: absolute;
    right: 0;
}

.mensajes .form-mensaje button svg {
    width: 20px;
    height: 20px;
    color: white;
}

.mensajes .form-mensaje textarea {
    outline: none;
    width: 90%;
    margin: 2px 5%;
    height: 30px;
    background-color: #f9fbfc;
    border: none;
    resize: none;
    box-shadow: 1px 1px 4px 1px #a8adb3;
    border-radius: 10px;
    padding: 2px;
}

.box-shadow {
    box-shadow: 0px 4px 5px 0px #cccdce;
}

.panel-notificaciones{
    flex-basis: 20%;
}

@media (max-width: 1020px) {
    .panel-chats {
        flex-basis: 30%;
    }
    .panel-notificaciones{
        display: none;
    }
    .mensajes {
        flex-basis: 70%;
        /* flex-grow: 12; */
        border-left: .5px solid #ececec;
        border-right: .5px solid #ececec;
    }
}

.panel-notificaciones > div{
    margin-top: 10px;
}

.panel-notificaciones > div > h3 {
    text-align: center;
}

.panel-notificaciones > div {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 43vh;
    overflow-y: scroll;
    border: .5px solid #ececec;
}

.panel-notificaciones .notificaciones .notificacion {
    box-shadow: 0px 0px 5px 2px #cccdce;
    display: flex;
    align-items: center;
    border-radius: 10px;
    padding: 2px;
}

.panel-notificaciones .notificaciones .notificacion .label-notifi{
    margin-left: 5px;
}

.panel-notificaciones .notificaciones .notificacion .label-notifi strong{
    color: #754cf1;
}

 .sugerencias .sugerencia > div {
    display: flex;
    align-items: center;
}

 .sugerencias .sugerencia {
    display: flex;
    box-shadow: 0px 0px 5px 2px #cccdce;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px;
    padding: 2px;
}

 .sugerencias .sugerencia .label-sugges{
    margin-left: 5px;
}

 .sugerencias .sugerencia button {
    background: linear-gradient(to right, #754cf1, #b555f7);
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
}


/* LOGIN */
.loginregister{
    background-color: hsl(204, 70%, 53%);
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.loginregister .move {
    position: absolute;
    z-index: 10;
    background-color: hsl(204, 70%, 65%);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 45vh;
    width: 41vw;
    border-radius: 5px;
    transform: translateX(20.5vw);
    transition: all 0.5s linear;
}

.loginregister .move button {
    color: white;
}

.loginregister .login , .register  {
    width: 40vw;
}
.loginregister .login form , .register form  {
    width: 100%;
    height: 45vh;
    background-color: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 5px;
}

.loginregister .login form input:not([type='submit']),  .register form input:not([type='submit']) {
    display: block;
    background-color: hsl(192, 15%, 94%);
    height: 30px;
    width: 90%;
    margin: 5px 5%;
    text-align: center;
}

.loginregister .login form input[type='submit'] , .register form input[type='submit']{
    display: block;
    background-color: hsl(204, 70%, 53%);
    height: 30px;
    width: 50%;
    margin: 5px 25%;
    text-align: center;
}

.loginregister .register form select {
    width: 75px;
    margin: auto;
}

.boton-logout{
    margin-top: 25px;
    color: whitesmoke;
    padding: 5px;
    background-color: hsl(194, 66%, 61%);
    box-shadow: 2px 2px 8px 1px hsl(194, 66%, 61%);
}


.imagen-registro {
    display: flex;
    width: 95%;
    margin: 0 2.5%;
}

.imagen-registro > div {
    flex-basis: 80%;
}

.imagen-registro .container-img {
    width: 10vw;
    height: 12vh;
    flex-basis: 20%;
}

.imagen-registro img {
    width: auto;
    height: 100%;
}